<template>
  <div class="umis-setting__cards">
    <el-card class="umis-setting__card-margin">
      <div slot="header">
        <span></span>
      </div>
      <el-switch
        v-model="isFormData"
        active-text="formdata"
        inactive-text="application/json"
        @change="handleFormTypeChange"
      />
    </el-card>
    <setting-api v-model="domains" :on-api-changed="handleApiChanged" />
    <setting-interceptor />
    <setting-style v-model="style" />
  </div>
</template>
<script>
import { Card as ElCard } from 'element-ui';
import { Switch as ElSwitch } from 'element-ui';

import SettingApi from './api';
import SettingStyle from './style';
import SettingInterceptor from './interceptor';

export default {
  name: 'UmisSettings',
  components: {
    ElCard,
    ElSwitch,
    SettingApi,
    SettingStyle,
    SettingInterceptor,
  },
  data() {
    const {
      isApiChanged,
      isFormData,
      style,
      script,
      domains,
    } = this.$umisConfig;

    return {
      isApiChanged,
      isFormData,
      style,
      script,
      domains,
    };
  },
  watch: {
    domains: {
      handler(val) {},
    },
  },
  methods: {
    handleFormTypeChange(val) {
      this.$saveInitFormType(this, val);
    },
    handleApiChanged() {
      this.isApiChanged = true;
    },
  },
};
</script>

<style lang="scss">
.umis-setting__cards {
  .umis-setting__card-margin {
    margin: 10px 0;
  }
  .umis-setting__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>
